















































































































































import Vue from "vue";
import Slider from "@/components/Slider.vue";
import headerComponent from "@/components/HeaderSection.vue"

export default Vue.extend({
    name: "home",
    components: {
        Slider,
        headerComponent,
        // SideMenu
        // ValidationProvider
    },
    data() {
        return {
            isDisabled: false,
            displaySidenav: false,
            displayLightbox: false,
            user: {
                nume: null,
                mailul: null,
                continut: null,
            },
            submitted: false
        }
    },
    computed: {
        checkForm: function () {
            if (this.user.nume && this.user.mailul && this.user.continut) {
                this.isDisabled = false;
                return false
            } else {
                this.isDisabled = true;
                return true
            }
        }
    }
});
